                                                <span class="systemName">{{item.systemName}}</span>
                                                <span class="workName">{{item.workName}}</span>
                                                <span>{{item.content}}</span>
                                            </router-link>
                                        </span>
                                    </div>
                                    <span :class="getClass(item.payIndication)">{{item.payIndication}}</span>
