<script>
import TabMenu from '../ui/tab/TabMenu.vue'

export default {
    name: 'SituationSection',
    components: {
        TabMenu
    }, 
    data() {
        return {
            tabs: [
                { name: 'tab1', label: '/ ' }, 
                { name: 'tab2', label: '  ' },
