            appliItem: [
                {id: 1, systemName: ' A', workName: '', content: "apache tomcat apache tomcat", date: '2024-12-10', payIndication: '1 ', link: '/detail/1'},
                {id: 2, systemName: ' B', workName: '', content: "apache tomcat apache tomcat", date: '2024-12-10', payIndication: ' 2 ', link: '/detail/2'},
