                {id: 3, content: 'e  ' },
                {id: 4, content: 'e  ' },
            ],
        }
    }, 
    methods: {
        getClass(text) {
            const baseClass = 'payIndication'; 

            switch (text) {
                case '1 ': 
                    return [baseClass, 'style1']; 
                case ' 2 ': 
                    return [baseClass, 'style2']; 
                default : 
                return [baseClass]; 
            }
